#root {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	height: 100vh;
}

* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	font-size: 1.5rem;
	font-family: 'Lato', sans-serif;
}

.calc {
	border: 1px solid #423437;
	display: inline-grid;
	grid-template-columns: repeat(4, 70px);
	grid-template-rows: 90px 70px 70px 70px 70px;
}

.calc__screen {
	grid-column: 1/5;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
	-ms-flex-align: center;
	    align-items: center;
	overflow: hidden;
	padding-right: 15px;
	font-size: 2rem;
	background: #423437;
	color: #fff;
}

.calc__screen--small-txt {
	font-size: 1rem;
}

.clear {
	grid-column: 1/4;
}

button {
	background-color: transparent;
	border-top: 1px solid #423437;
	border-right: 1px solid #423437;
	border-left: none;
	border-bottom: none;
	cursor: pointer;
	-webkit-transition: all 0.4s;
	-o-transition: all 0.4s;
	transition: all 0.4s;
}

button:focus {
	outline: none;
}

button:active {
	-webkit-box-shadow: inset 0 0 5px 3px #EB0751;
	        box-shadow: inset 0 0 5px 3px #EB0751;
}

.calc__symbol {
	border-right: none;
	background: #EB0751;
	color: #fff;
}

.activeEquationBtn {
	background: #fff;
	color: #EB0751;
}



